import { h } from "preact"
import style from "./style.css"
const testUrls: {
   name: string,
   url: string,
   description: string
}[] = [
   {
      name: "autism",
      url: "autism",
      description: "Checks if you have autism."
   },
   {
      name: "adhd",
      url: "adhd",
      description: "Checks if you have adhd."
   },
   {
      name: "dyslexia",
      url: "dyslexia",
      description: "Checks if you dyslexia"
   }
]
const TestsComponents = () => {
   const elements = []
   for (const test of testUrls){
      elements.push(
         <Resource
            title={test.name}
            description={test.description}
            link={`/test/${test.url}`}
         />
      )
      elements.push(<br />)
   }
   return <div id="tests" class={style.home}>
      <h1>Select a test</h1>
      <section>
         {elements}
      </section>
   </div>
}
interface ResourceProps {
	title: string;
	description: string;
	link: string;
}

const Resource = (props: ResourceProps) => {
	return (
		<a href={props.link} class={style.resource}>
			<h2>{props.title}</h2>
			<p>{props.description}</p>
		</a>
	)
}
export default TestsComponents